import React, { Component } from 'react';
import {Media,Modal,ModalBody,ModalHeader} from 'reactstrap';
import {Card,CardImg,CardText,CardBody,CardTitle,Breadcrumb,BreadcrumbItem,Label,Row,Col,Button} from 'reactstrap';
import {Link} from 'react-router-dom';
import { LocalForm ,Errors,Control} from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform,Fade,Stagger} from 'react-animation-components';


const required = (val) => val && val.length; //value > 0
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);

class CommmentForm extends Component{
    constructor(props){
        super(props);

        this.state={
            toggleCommentForm:false
        }
    }
    
    toggleCommentFormModal = () => {
        this.setState({
            toggleCommentForm:!this.state.toggleCommentForm
        });
    }
    handlerSubmit = (values) =>{
        this.toggleCommentFormModal();
        this.props.postComment(this.props.dishId,values.rating,values.author,values.comment);
    }

    render(){
        return(
            <React.Fragment>
                <button  className="btn btn-outline-secondary" onClick={this.toggleCommentFormModal}>
                    <span className="fa fa-pencil fa-lg"></span>Submit Comment
                </button>

                <Modal isOpen={this.state.toggleCommentForm} toggle={this.toggleCommentFormModal}>
                    <ModalHeader toggle={this.toggleCommentFormModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={this.handlerSubmit}>
                            <Row className="form-group">
                                <Label htmlFor="rating" md={12}>Rating </Label>
                                <Col md={12}>
                                    <Control.select model=".rating"
                                        className="form-control"
                                        name="rating"
                                        id="rating"
                                        validators={{
                                            required
                                        }}
                                    >
                                        <option>Please Select</option>
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Control.select>
                                    <Errors
                                        className="text-danger"
                                        model=".rating"
                                        show="touched"
                                        messages={{
                                            required: 'Required'
                                        }}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="author" md={12}>Your Name</Label>
                                <Col>
                                        <Control.text model=".author"
                                            className="form-control"
                                            name="author"
                                            id="author"
                                            validators={{
                                                required, maxLength:maxLength(15) , minLength : minLength(3)
                                            }}
                                        >
                                        </Control.text> 
                                        <Errors
                                            className="text-danger"
                                            model=".author"
                                            show="touched"
                                            messages={{
                                                required : 'Required',
                                                minLength : 'Must be atleast 2 characters',
                                                maxLength : 'Must be <= 15 characters'
                                            }}
                                        />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="comment" md={12}>Comment</Label>
                                <Col md={12}>
                                    <Control.textarea model=".comment" id="comment" name="comment"
                                        rows="6"
                                        className="form-control"
                                        validators={{
                                            required
                                        }}
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".author"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                        }}
                                    />
                                </Col>

                            </Row>
                            <Row className="form-group">
                                <Col>
                                    <Button type="submit" color="primary">
                                        Submit 
                                    </Button>
                                </Col>
                            </Row>
                        </LocalForm>
                    </ModalBody>
                </Modal>

            </React.Fragment>
        );
    }
}
function RenderComments(comments,postComment,dishId){
    const commentSection = comments.map( (CurrentComment) => {
        return (
            <Fade in>
            <div key={CurrentComment.id} >
               <Media tag='li' className="m-1">
                    <Media Body>
                        <div className="row">
                            <div className="col-12 mb-3">
                                {CurrentComment.comment}
                            </div>
                            <div className="col-12 mb-3">
                               &nbsp; --{CurrentComment.author},{new Intl.DateTimeFormat('en-US',{year:'numeric', month:'short',day:'2-digit'}).format(new Date(Date.parse(CurrentComment.date)))}
                            </div>
                       </div>
                    </Media>
                </Media>
            </div>
            </Fade>
        );
    } );

    if (comments!=null){
        return(
            <div className="container">
                <div className="row">
                    <h4>Comments</h4>
                    <ul className="list-unstyled"> 
                        <Stagger in>
                            {commentSection} 
                        </Stagger>
                        
                    </ul>
                    <CommmentForm postComment={postComment} dishId={dishId}/>
                </div>
            </div>
        );
    }else{
        return (
            <div></div>
        );
    }
}

function RenderDish(dish,isLoading,errMess){
    if (isLoading) {
        return(
            <div className="container">
                <div className="row">            
                    <Loading />
                </div>
            </div>
        );
    }
    else if (errMess) {
        return(
            <div className="container">
                <div className="row">            
                    <h4>{errMess}</h4>
                </div>
            </div>
        );
    }
    else if(dish!=null){
            return(
                <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg top width="100%" src = {baseUrl + dish.image} alt={dish.name}/>
                    <CardBody>
                        <CardTitle>
                            {dish.name}
                        </CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
                </FadeTransform>      
            );
        }else{
            return(
                <div></div>
            );
            }
}
const DishDetail = (props)=>{
    if(props.dish==null){
        return (<div></div>);
    }
    
    return (
        
        <div className="container">
            <div className="row">
                    <Breadcrumb>

                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>                
                </div>
            <div className="row">
                 <div className="col-12 col-md-5 m-1">
                     
                     {RenderDish(props.dish,props.isLoading,props.errMess)}
                 </div>
                 <div className="col-12 col-md-5 m-1">
                     
                     {RenderComments(props.comments,props.postComment,props.dish.id)}
                 </div>
            </div>
           
            
            
        </div>
    );

}

export default DishDetail;